import React, { Suspense } from "react";
import Video from "../../../Elements/Media/Video";
import { isTagEmbed, isTagShop,isSocialEmbed } from "../../../../../actions/api";
import Image from "../../../Elements/Media/Image";
import MediaIcon from "../../../Elements/MediaIcon/MediaIcon";
const Like = React.lazy(() => import('../../../Elements/Reactions/Like'));
const Media = ({ itemData, wallID, ownerId, wall, isCover, ThemeRule }) => {
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed ? ThemeRule.aspectImageRatio ? ThemeRule.aspectImageRatio : 100 : 100
  const mediaIcon = itemData.hotspot ? false : true
  return <div className="tb_g_h_media_wrap">
 {isSocialEmbed ? <Suspense fallback={<></>}><Like postId={itemData.id} wallID={wallID} UserDetail={wall.UserDetail} /></Suspense> : null}
    {isSocialEmbed ? <MediaIcon itemData={itemData}  IconClass="tb_g_h_media_icon" show={isSocialEmbed} isCenter={false} /> : null}
    {autoPlay ?
      <Video VideoClass="tb_g_h_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} wall={wall} size={size} isCover={isCover} muted={true} autoPlay={autoPlay} handleVideoEnded={() => { }} />
      :
      <Image ImageClass="tb_g_h_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} hotspot={true}/>
    }
  </div>
}
export default Media